section :global(button[data-membership-type='suite'][data-plan-id='pl_LdtycZ']),
section :global(button[data-membership-type='suite'][data-plan-id='pl_Te8nj-']){
    pointer-events: none;
    opacity: 0.5;
}*/

section :global(button[data-membership-type='suite'][data-plan-id='pl_-He4DX']),
section :global(button[data-membership-type='suite'][data-offer-type='essential-or-low']){
    pointer-events: all;
    opacity: 1;
}
</style>