import { turpial } from "./turpial";

function Connect3Pod(urls, {ready, data, method, options}){
    method = method||"POST";
    ready = ready||function(){};
    options = options||{};
    data = data||{};
    if(typeof urls === "string"){
        urls = [urls];
    }
    
    turpial.fetch({
        url: urls,
        method: method,
        options: options,
        type: "text",
        data: JSON.stringify(data),
        ready: (data)=>{
            ready(data.filter((result)=>{                
                try {
                    result = JSON.parse(result);
                    return result;                    
                } catch (error) {
                    console.error(error)
                }
            }));
        }
    })
}

export {Connect3Pod}

/**
 * Example: 
 * Connect3Pod([HOST+"private/api/v1/admin/connect"], {
        data: data,
        ready: (results)=>{
            console.log(results);
        },
    })
 */