function storeForm() {
	const { subscribe, set, update } = writable(StoreFormData);
    const form = {
        subscribe,
        update_raw: update,
        update: function(field, dataForm, merge_with){
            merge_with = merge_with ?? null;
            update((data)=>{
                if(merge_with !== null){
                    Object.values(dataForm).map((item, key)=>{
                        let name = Object.keys(dataForm)[key];
                        merge_with[name] = item;
                    })
                    dataForm = merge_with;
                }
                

                data[field] = dataForm;
                return data;
            })
        },
    }
    return form;
}

window.StoreFormData = StoreFormData;

export const StoreForm = storeForm();